:root {
	--font-family: "Open Sans", sans-serif;
	--font-p: "Rubik", sans-serif;
	--size-p: 1.4rem;
	--primary: #2f2e2e;
	--primary-dark: #104e61;
	--dark: #161616;
	--backgroung-light: #e9e6e6;
	--background-form: #f7f0da;
	--text-color: #444;
	--border-color-input: #cccccc;
	--text-color-light: #fff;
	--text-color-input: rgb(233, 231, 231);
	--text-color-h1: rgb(237, 158, 6);
	--text-color-label: rgb(20, 118, 168);
	--box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
		rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
	--border: 1px solid rgb(83, 81, 81);
	--alert: #e74c3c;
	--font-h1: "Lobster", cursive;
}

.wp-block-create-block-recettes-app {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
}

/* Uniformiser la taille de l'image principale de la recette */
.single-recette .image-container {
	width: 100%;
	height: auto;
	max-height: 400px; /* Ajustez la hauteur maximale selon vos besoins */
	overflow: hidden;
	position: relative;
	background-size: cover;
	transition: transform 1s;
	border-radius: 15px; /* Moins de courbure pour une page individuelle */
	margin-bottom: var(
		--wp--preset--spacing--30
	); /* Espace entre l'image et le contenu */
	box-shadow: var(--box-shadow);
}

.single-recette .image-container img {
	position: relative; /* Changement pour rendre l'image visible */
	width: 100%;
	height: 100%; /* S'assurer que l'image conserve son aspect ratio */
	transform: translate(0, 0); /* Pas de translation initiale */
	object-fit: cover; /* Recadrage automatique de l'image pour couvrir tout le conteneur */
	transition: transform 0.3s ease-in-out;
	display: block; /* Assure que l'image est un bloc et ne dépasse pas */
}

.single-recette .image-container:hover img {
	transform: scale(1.1); /* Zoom au survol */
}

/* Styles spécifiques pour la page de la recette */
.single-recette .wp-block-post-title {
	font-size: 3rem;
	text-align: left;
	margin-top: var(--wp--preset--spacing--20);
	margin-bottom: var(--wp--preset--spacing--10);
	color: var(--text-color-h1);
	font-family: var(--font-h1);
}

.single-recette .wp-block-post-content {
	font-size: var(--size-p);
	color: var(--text-color);
	line-height: 1.6;
	font-family: var(--font-p);
	margin-bottom: var(--wp--preset--spacing--20);
}

.single-recette .custom-fields {
	margin-top: var(--wp--preset--spacing--30);
}

.single-recette .custom-fields h3 {
	font-size: var(--wp--preset--font-size--medium);
	margin-bottom: var(--wp--preset--spacing--10);
	color: var(--text-color-h1);
	font-family: var(--font-h1);
}

.single-recette .custom-fields ul,
.single-recette .custom-fields ol {
	padding-left: 1.5rem;
	margin-bottom: var(--wp--preset--spacing--20);
}

.single-recette .custom-fields ul li,
.single-recette .custom-fields ol li {
	font-size: var(--wp--preset--font-size--medium);
	color: var(--text-color);
	margin-bottom: var(--wp--preset--spacing--10);
}

.single-recette .button {
	background-color: #555555;
	font-size: 1.6rem;
	color: wheat;
	padding: 1rem;
	box-shadow: var(--box-shadow);
	text-decoration: none;
	margin-top: var(--wp--preset--spacing--20);
	display: inline-block;
	border-radius: 5px;
}
